import BigNumber from 'bignumber.js'
import { ethers } from 'ethers'

export const approve = async (lpContract, masterChefContract, account) => {
  return lpContract.methods
    .approve(masterChefContract.options.address, ethers.constants.MaxUint256)
    .send({ from: account })
}




export const exchangeBuyWFTM = async (contract, amount, account) => {
  return contract.methods
    .buyWithBNB()
    .send({ from: account, value: new BigNumber(amount) })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const swapV1toV2 = async (contract, depositToken, amount, account) => {
  return contract.methods
    .swapToken(depositToken, amount)
    .send({ from: account })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const exchangeSale = async (contract, amount, account) => {
  return contract.methods
    .MigrateToGold(amount)
    .send({ from: account })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const stake = async (masterChefContract, pid, amount, account) => {
  return masterChefContract.methods
    .deposit(pid, new BigNumber(amount).times(new BigNumber(10).pow(18)).toString())
    .send({ from: account })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

